<template>
	<div class="body">
		<div class="body_header flex_start">
			<i class="icon iconfont icon-guanligongsigaikuang"></i>
			<span>管理公司概况</span>
		</div>
		<div class="search_row_box flex_start">
			<div class="search_box">
				<el-autocomplete v-model="searchCompanyValue" :fetch-suggestions="querySearch1" :clearable='true' prefix-icon="el-icon-search" class="search_select" placeholder="公司名称"></el-autocomplete>
			</div>
			<div class="search_box">
				<el-autocomplete v-model="searchCreaterValue" :fetch-suggestions="querySearch2" :clearable='true' class="search_select" placeholder="数据创建人"></el-autocomplete>
			</div>
			<div class="search_box">
				<el-select v-model="searchStatusValue" clearable placeholder="备案状态">
			    		<el-option v-for="item in allFilingStatus" :key="item.id" :label="item.name" :value="item.id">
				    </el-option>
				</el-select>
			</div>
			<div class="date_box">
				<el-date-picker v-model="BeginDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd" :clearable="false" class="" :editable='false'>
				</el-date-picker>
			</div>
			<div class="date_middle_text">
				至
			</div>
			<div class="date_box">
				<el-date-picker v-model="LastDate" type="date" placeholder="结束日期" value-format="yyyy-MM-dd" :clearable="false" class="" :editable='false'>
				</el-date-picker>
			</div>
			<div @click="searchCompany" class="search_btn cursor">
				搜索
			</div>
		</div>
		<div class="line_box">
			<div class="line"></div>
		</div>
		<div class="title_group_box">
			<div class="page_name ">基金管理公司总数（&nbsp;{{total}}&nbsp;）</div>
			<div class="flex_end">
				<div @click="downloadManageCompanyInfoExcel" class="table-group-title-btn btn-blue-border">
					<span class="iconfont icon-daoru"></span>导出excel表格
				</div>
				<div @click="downloadManageCompanyTempletExcel" class="table-group-title-btn btn-blue-border">
					<span class="iconfont icon-daoru"></span>下载管理公司信息模版
				</div>
				<div class="">
					<label for="upload-fund-input">
						<div class="table-group-title-btn btn-blue-border">
							<span class="iconfont icon-daochu"></span>导入管理公司信息模块
						</div>
					</label>
					<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadManageCompanyInfoExcel($event)" />
				</div>
				<div class="table-group-title-btn btn-blue" @click="addManageCompany">
					<span class="iconfont icon-jia"></span>添加管理公司
				</div>
			</div>
		</div>

		<!-- 数据表格开始 -->
		<div class="historyapply-group-tablebox table-group-tablebox">
			<div class="table">
				<el-table ref="multipleTable" :data="datalist" border style="width: 100%">
					<el-table-column prop="" label="序号" width="70">
						<template slot-scope="scope">
							{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
						</template>
					</el-table-column>
					<el-table-column sortable prop="company_name" label="公司名称" width="300">
					</el-table-column>
					<el-table-column sortable prop="id" label="ID" width="80">
					</el-table-column>
					
					<el-table-column sortable prop="legal_representative" label="法定代表人" width="150">
					</el-table-column>
					
					<el-table-column sortable prop="reg_capital" label="注册资本" width="150">
					</el-table-column>
					<el-table-column sortable prop="windcontrol_name" label="运营风控负责人" width="150">
					</el-table-column>
					<el-table-column sortable prop="manager_code" label="私募基金管理人编号" width="200">
					</el-table-column>
					<el-table-column sortable prop="manage_fund_num" label="在管基金数量" width="150">
					</el-table-column>
					<el-table-column sortable prop="manage_scale" label="管理总规模（人民币/万元）" width="220">
					</el-table-column>
					
					<el-table-column sortable prop="founding_time" label="成立时间" width="150">
					</el-table-column>
					<el-table-column sortable prop="create_user_name" label="数据创建人" width="150">
					</el-table-column>
					<el-table-column sortable prop="record_state_name" label="备案状态" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="250">
						<template slot-scope="scope">
							<el-button @click="setUserPower(scope.row)" type="text" size="small">设置权限</el-button>
							<el-button @click="toCompanyRun(scope.row)" type="text" size="small">公司运行</el-button>
							<el-button @click="viewCompany(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="deleteCompany(scope.$index,scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    				</el-pagination>
			</div>
		</div>
		<!--数据表格结束-->

		<!--  设置权限弹窗开始-->
		<el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="isSetPowerDialogShow=false">取消</button>
				<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
			</div>
		</el-dialog>
		<!--设置权限弹窗结束-->
	</div>
</template>
<script>
	import setPower from '../../components/setPower.vue'
	import { getFundCompanyListApi, getHavePowerUsersApi, setPowerSubmitApi,deleteFundCompanyApi,downloadManageCompanyTempletExcelApi,downloadManageCompanyInfoExcelApi, uploadManageCompanyInfoExcelApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	export default {
		data() {
			return {
				currentPage: 1,
				pageSize: 10,
				total: 0,
				
				allCompanyList: [],
				allCreaterList: [],
				allFilingStatus: [],
				searchCompanyValue: '',
				searchCreaterValue: '',
				searchStatusValue: '',
				searchStatusId: '',
				
				company_id: '',
				datalist: [], //基金列表
				total: '',
				BeginDate: '', //开始时间
				LastDate: '', //结束时间
				isSetPowerDialogShow: false,
				havePowerUserIds: [],
				subComponentPostIds: [],
				ids: '', //设置权限时选择的用户ids
			}
		},
		created() {
			if(!this.$route.query.from) {
				this.from = this.$route.query.from
			} else {
				this.from = ''
			}
			this.getCompantDatalist();
		},
		components: {
			setPower
		},
		computed: {},
		mounted() {},
		methods: {
			getCompantDatalist(is_init_page) {
				if (is_init_page) {
					this.currentPage = 1;
				}
				getFundCompanyListApi({
					page: this.currentPage,
					size: this.pageSize,
					company_name: this.searchCompanyValue,
					create_user_name: this.searchCreaterValue,
					record_state: this.searchStatusValue,
					start_founding_time: this.BeginDate,
					end_founding_time: this.LastDate
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.total = res.data.payload.count;
						this.datalist = res.data.payload.data;
						if (!this.allCompanyList.length) {
							this.allCompanyList = res.data.payload.all_company_name;
							this.allCreaterList = res.data.payload.all_create_user_name;
							this.allFilingStatus = res.data.payload.all_record_state;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			searchCompany() {
				let data1 = new Date(this.BeginDate);
				let data2 = new Date(this.LastDate);
				if(data1.getTime() > data2.getTime()) {
					dialogConfirm("info", "开始时间不能大于结束时间");
					return false;
				} else {
					this.getCompantDatalist(1)
				}
			},
			chooseCompanyName(e){
				console.log(this.searchCompanyValue);
				console.log(e);
			},
			// 输入搜索的回调方法函数
			querySearch1(queryString, cb) {
				var restaurants = this.allCompanyList;
				var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
				// 调用 callback 返回建议列表的数据
				cb(results);
			},
			querySearch2(queryString, cb) {
				var restaurants = this.allCreaterList;
				var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
				// 调用 callback 返回建议列表的数据
				cb(results);
			},
			createFilter(queryString) {
				return (restaurant) => {
          			return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        			};
      		},
      		getStatusId(e){
      			console.log(e);
      			this.searchStatusId = e.id;
      		},
			deleteCompany(index, row) {
				console.log(row);
				var id = row.id;
				if (row.auth.delete_operate) {
					dialogConfirmHasbtn('确定要删除管理公司吗？').then(() => {
						this.toDeleteCompany(id)
					}).catch(() => {});
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			toDeleteCompany(id){
				deleteFundCompanyApi({
					id: id
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.getCompantDatalist()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			viewCompany(row) {
				var id = row.id;
				if (row.auth.check_operate) {
					this.$router.push({
						name: 'manageCompanyDetail',
						query: {
							id: id
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			toCompanyRun(row){
				var id = row.id;
				if (row.auth.auth_operate) {
					this.$router.push({
						name: 'manageCompanyRun',
						query: {
							id: id
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			setUserPower(row) {
				this.company_id = row.id;
				if(row.auth.auth_operate) {
					this.getHavePowerUsers(row.id);
				} else {
					dialogConfirm("info", "您没有设置权限，请联系管理员！");
				}
			},
			getHavePowerUsers(company_id) {
				getHavePowerUsersApi({
					type: 1,
					data_id: company_id
				}).then(res => {
					console.log(res.data);
					if(res.data.msgNo == 1) {
						this.havePowerUserIds = res.data.payload.ids;
						this.ids = res.data.payload.ids.join(',');
						this.isSetPowerDialogShow = true;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取设置权限组件返回来的ids
			getSetPowerIds(idNodes) {
				console.log(idNodes);
				this.subComponentPostIds = [];
				for(let i = 0, max = idNodes.length; i < max; i++) {
					if(this.subComponentPostIds.indexOf(idNodes[i]) == -1) {
						this.subComponentPostIds.push(idNodes[i])
					}
				}
				var postIds = this.decodeSelectedUserId(this.subComponentPostIds);
				this.ids = postIds.join(',');
			},
			//给加标识的员工id进行解码还原为id数组
			decodeSelectedUserId(data) {
				var user_ids = '';
				var selectedEmployeeId = [];
				var splitArr = [];
				for(var i = 0, max = data.length; i < max; i++) {
					splitArr = data[i].id.split('-');
					if(selectedEmployeeId.indexOf(splitArr[0]) == -1) {
						selectedEmployeeId.push(splitArr[0]);
					}
				}
				return selectedEmployeeId
			},
			setPowerSubmit() {
				setPowerSubmitApi({
					table_name: 'sg_company_manager',
					type: 1,
					data_id: this.company_id,
					ids: this.ids
				}).then(res => {
					console.log(res.data);
					if(res.data.msgNo == 1) {
						this.isSetPowerDialogShow = false;
						dialogConfirm("success", res.data.message);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadManageCompanyTempletExcel() {
				downloadManageCompanyTempletExcelApi()
			},
			downloadManageCompanyInfoExcel(){
				downloadManageCompanyInfoExcelApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//导入Excel文件
			uploadManageCompanyInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				uploadManageCompanyInfoExcelApi({

				}, file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.getCompantDatalist()
					} else {
						dialogConfirm('info', res.data.message);
					}
					inputDOM.value = '';
				})
			},
			

			/*------------------------------------弹窗---------------------------------------------*/
			addManageCompany() {
				this.$router.push({
					name: 'manageCompanyAdd'
				})
			},
			//弹窗关闭确认
			dialogClose() {
				let param = this.dialogParam;
				if(param.name != '' || param.phone != '' || param.email != '' || param.leader != '' || param.depart_id != '' || param.position != '' || param.entryTime != '') {
					dialogConfirmHasbtn('确定放弃修改吗？').then(() => {
						this.fileuploadDialog = false
					}).catch(() => {});
				} else {
					this.fileuploadDialog = false;
				}
			},

			//设置权限弹窗确定按钮
			commitRule() {
				let str = this.$refs.tree.getCheckedKeys();
				// console.log(str.length);
				// console.log(this.ruleList);
				str.forEach(ruleID => {
					// console.log(ruleID);
					this.ruleList.forEach(item1 => {
						if(item1.child != undefined) {
							item1.child.forEach(item2 => {
								if(item2.id == ruleID) {
									if(str.indexOf(item1.id) == -1) {
										str.push(item1.id);
									}
								} else {
									if(item2.child != undefined) {
										item2.child.forEach(item3 => {
											if(item3.id == ruleID) {
												if(str.indexOf(item2.id) == -1) {
													str.push(item2.id);
												}
											}
										});
									}
								}
							});
						}
					});
				});
				// console.log(str);
				str = str.join(',')

				// return false;
				// console.log(this.setRuleId);
				editUserRule({
					user_id: this.setRuleId,
					rule: str,
				}).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data);
						dialogConfirm('success', '权限设置成功！');
						this.ruleDialog = false;
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},

			//关闭弹窗之前调用
			ruleClose() {
				this.ruleDialog = false;
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getCompantDatalist()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getCompantDatalist()
			},
		},
	}
</script>
<style lang="scss" scoped>
	@import '../../style/modal.scss';
	.body{
		box-sizing: border-box;
		padding: 0 30px;
	}
	.body_header i{
		font-size: 18px;
	}
	.line_box {
		margin-bottom: 30px;
	}
	
	.line {
		height: 1px;
		background-color: #E5E5EA;
	}
	
	.table-group-title-btn {
		.icon-jia {
			width: 14px;
			height: 14px;
			font-weight: bold;
		}
	}
	
	.peopleSelectTitle-text {
		font-size: 18px;
		color: #3C98E8;
		font-weight: bold;
		line-height: 40px;
		margin-right: 10px;
	}
	
	.peopleSelectTitle {
		border-bottom: 1px solid #dcdfe6;
	}
	
	.table-span {
		background: #E7F4FF;
		color: #3C98E8;
		font-size: 12px;
		margin-right: 5px;
		margin-bottom: 5px;
		padding: 3px 5px;
	}
	
	.dialog-btn-submit {
		width: 94px;
		height: 36px;
		font-size: 14px;
		float: right;
		margin-left: 20px;
	}
	/*新增样式*/
	
	.table_group_title_delete {
		margin-right: 15px;
		.icon-daoru {
			color: #3C98E8;
			font-size: 20px;
		}
	}
	
	.page_name {
		display: flex;
		align-items: center;
		font-size: 12px;
		color: #6292F4;
	}
	
	.title_group_box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 20px;
	}
	.search_row_box .search_btn{
	    margin-left: 76px;
	}
	/*设置权限弹出框*/
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	/*设置权限弹出框结束*/
</style>


